import {Component, OnInit} from '@angular/core';
import { AttractionService } from '../../services/attraction.service';
@Component({
  selector: 'admin-attractions',
  templateUrl: './attractions.component.html',
  //styleUrls: ['./attraction.component.css']
})

export class AttractionsComponent implements OnInit {

  attractions: any;
  museums: any;

  selected:boolean = false;
  selectedAttraction: string;

  searchString: string;

  constructor(private service: AttractionService) {}

  ngOnInit() {
    this.load();
  }

  load(): void {
    this.service.getCitiesAttractions().subscribe(data => {this.attractions = data; console.log(this.attractions);});
    this.service.getMuseums().subscribe(data => {this.museums = data; console.log(this.museums);});
  }

  select(attraction) {
    console.log(attraction);
    this.selectedAttraction = attraction;
    this.selected = true;
  }
}



