export class MuseumAttraction {
  name:string;
}

export class Room {
  name:string;
  attractions: MuseumAttraction[] = [];
  adjacent: Room[] = [];
}

export class Museum {
  name: string;
  latitude: number;
  longitude: number;
  radius: number;
  rooms: Room[] = [];
  start: Room;
  end: Room;
}



