import {Component, OnInit} from '@angular/core';
import { AttractionService } from '../../services/attraction.service';
@Component({
  selector: 'admin-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

  attractions: any;
  museums: any;

  markers: marker[] = [];
  lat: number = 41.90;
  lng: number = 12.4963;

  constructor(private service: AttractionService) {}

  ngOnInit() {
    this.load();
  }

  load(): void {
    this.service.getCitiesAttractions().subscribe(data => {
      this.attractions = data;
      console.log(this.attractions);
      for(let attraction of this.attractions) {
        console.log(attraction);
        let lat: number  = +attraction['latitude']
        let lng: number  = +attraction['longitude']
        let label: string = attraction['name'];
        let marker: marker = {
          lat,lng,label
        }
        this.markers.push(marker);
      }
      console.log(this.markers);
    });
    this.service.getMuseums().subscribe(data => {this.museums = data; console.log(this.museums);});
  }
}

interface marker {
  lat: number;
  lng: number;
  label: string;
}



